"use client";
import { useState, useEffect } from "react";
import { CheckCircle, RefreshCw, ArrowLeft, Trophy, User, ShoppingCart, DollarSign } from "lucide-react";

const API_URL = "http://localhost:8080/api";

type MatchedOrder = {
  id: number;
  seller_price: number;
  buyer_price: number;
  seller_qty: number;
  buyer_qty: number;
  seller_time: string;
  buyer_time: string;
  seller_date: string;
  buyer_date: string;
  incoming_time: string;
  outgoing_time: string;
  time_taken: string;
  status: string;
  transaction_type: number;
  buyer_user_id: number;
  seller_user_id: number;
  buyer_transaction_id: string;
  seller_transaction_id: string;
};

export default function MatchedOrdersPage() {
  const [data, setData] = useState<MatchedOrder[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [userIdFilter, setUserIdFilter] = useState<number | null>(null);
  const [currentUser, setCurrentUser] = useState<any>(null);

  useEffect(() => {
    // Get user from localStorage
    const userStr = localStorage.getItem("user");
    if (userStr) {
      try {
        const userData = JSON.parse(userStr);
        setCurrentUser(userData);
      } catch (e) {
        console.error("Error parsing user data:", e);
      }
    }

    // Check if URL has user_id parameter
    const params = new URLSearchParams(window.location.search);
    const userId = params.get("user_id");
    if (userId) {
      setUserIdFilter(parseInt(userId));
    }
  }, []);

  const getTransactionTypeName = (type: number) => {
    if (type === 0) return "Cash";
    if (type === 1) return "Crypto";
    if (type === 2) return "Both";
    return "Unknown";
  };

  const formatTime = (timeStr: string) => {
    try {
      const date = new Date(timeStr);
      return date.toLocaleTimeString('en-US', { 
        hour: '2-digit', 
        minute: '2-digit',
        hour12: true 
      });
    } catch {
      return timeStr;
    }
  };

  const formatDate = (dateStr: string) => {
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('en-US', { 
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      });
    } catch {
      return dateStr;
    }
  };

  const fetchMatchedOrders = async () => {
    setLoading(true);
    setError(null);
    
    try {
      let endpoint = `${API_URL}/matched-orders`;
      
      // If userIdFilter is set, fetch user-specific matches
      if (userIdFilter) {
        endpoint = `${API_URL}/matched-orders/user/${userIdFilter}`;
      }
      
      const response = await fetch(endpoint);
      if (response.ok) {
        const result = await response.json();
        setData(result || []);
      } else {
        setError(`Failed to fetch data: ${response.statusText}`);
      }
    } catch (err) {
      setError("Network error. Please check if backend is running.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchMatchedOrders();
    
    // Auto-refresh every 3 seconds
    const interval = setInterval(fetchMatchedOrders, 3000);
    return () => clearInterval(interval);
  }, [userIdFilter]);

  // Helper to determine user's role in a match
  const getUserRole = (match: MatchedOrder) => {
    if (!userIdFilter) return null;
    if (match.buyer_user_id === userIdFilter) return "buyer";
    if (match.seller_user_id === userIdFilter) return "seller";
    return null;
  };

  const getPageTitle = () => {
    if (userIdFilter && currentUser) {
      return `My Matched Orders (${currentUser.username})`;
    }
    return "All Matched Orders";
  };

  const getBorderColor = () => {
    if (userIdFilter) return "border-blue-500 bg-gradient-to-br from-blue-50 to-white";
    return "border-green-500 bg-gradient-to-br from-green-50 to-white";
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <div className="mb-6 flex items-center justify-between">
          <button
            onClick={() => window.history.back()}
            className="flex items-center gap-2 px-4 py-2 bg-white border-2 border-black rounded-lg hover:bg-gray-100 transition-all"
          >
            <ArrowLeft size={20} />
            <span className="font-semibold">Back</span>
          </button>
          
          <button
            onClick={fetchMatchedOrders}
            disabled={loading}
            className="flex items-center gap-2 px-4 py-2 bg-black text-white rounded-lg hover:bg-gray-800 transition-all disabled:opacity-50"
          >
            <RefreshCw size={20} className={loading ? "animate-spin" : ""} />
            <span className="font-semibold">Refresh</span>
          </button>
        </div>

        <div className={`bg-white rounded-3xl shadow-lg border-2 p-8 ${getBorderColor()}`}>
          <div className="flex items-center gap-3 mb-6">
            {userIdFilter ? (
              <User className="text-blue-600" size={40} />
            ) : (
              <CheckCircle className="text-green-600" size={40} />
            )}
            <h1 className="text-3xl font-bold text-black">{getPageTitle()}</h1>
            <span className={`ml-auto px-3 py-1 ${userIdFilter ? 'bg-blue-400' : 'bg-green-400'} text-black text-xs font-bold rounded-full`}>
              AUTO-UPDATING
            </span>
          </div>

          {loading && (
            <div className="text-center py-12">
              <RefreshCw className="animate-spin mx-auto mb-4 text-gray-400" size={48} />
              <p className="text-gray-600">Loading matched orders...</p>
            </div>
          )}

          {error && (
            <div className="bg-red-50 border-2 border-red-200 rounded-lg p-6 text-center">
              <p className="text-red-600 font-semibold">{error}</p>
            </div>
          )}

          {!loading && !error && data.length === 0 && (
            <div className="bg-gray-50 border-2 border-gray-200 rounded-lg p-12 text-center">
              <Trophy className="mx-auto mb-4 text-gray-300" size={64} />
              <p className="text-gray-600 text-lg font-semibold">
                {userIdFilter ? "No matches found for your account" : "No matched orders yet"}
              </p>
              <p className="text-gray-500 text-sm mt-2">
                {userIdFilter 
                  ? "Your orders haven't been matched yet. Keep trading!" 
                  : "Matches will appear here when buyer and seller prices align"}
              </p>
            </div>
          )}

          {!loading && !error && data.length > 0 && (
            <div className="overflow-x-auto">
              <table className="w-full border-collapse">
                <thead>
                  <tr className={`border-b-2 ${userIdFilter ? 'bg-blue-100 border-blue-500' : 'bg-green-100 border-green-500'}`}>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">ID</th>
                    {userIdFilter && <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Your Role</th>}
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer ID</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller ID</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer TXN</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller TXN</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller Price</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer Price</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Seller Qty</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Buyer Qty</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Time Taken</th>
                    <th className="px-4 py-3 text-left text-sm font-bold text-black uppercase tracking-wider">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((row, idx) => {
                    const userRole = getUserRole(row);
                    const isBuyer = userRole === "buyer";
                    const isSeller = userRole === "seller";
                    
                    return (
                      <tr
                        key={row.id}
                        className={`border-b border-gray-200 hover:bg-gray-100 transition-colors cursor-pointer ${
                          userIdFilter
                            ? isBuyer 
                              ? "bg-blue-50" 
                              : isSeller 
                              ? "bg-purple-50" 
                              : "bg-white"
                            : idx % 2 === 0 
                            ? "bg-white" 
                            : "bg-green-50"
                        }`}
                      >
                        <td className="px-4 py-3 text-sm text-gray-800 font-semibold">{String(row.id).padStart(3, '0')}</td>
                        
                        {userIdFilter && (
                          <td className="px-4 py-3 text-sm">
                            {isBuyer && (
                              <span className="flex items-center gap-1 px-2 py-1 bg-blue-500 text-white text-xs font-bold rounded-full w-fit">
                                <ShoppingCart size={12} />
                                BUYER
                              </span>
                            )}
                            {isSeller && (
                              <span className="flex items-center gap-1 px-2 py-1 bg-purple-500 text-white text-xs font-bold rounded-full w-fit">
                                <DollarSign size={12} />
                                SELLER
                              </span>
                            )}
                          </td>
                        )}
                        
                        <td className="px-4 py-3 text-sm">
                          <span className={`px-2 py-1 rounded-full text-xs font-semibold ${
                            isBuyer ? 'bg-blue-200 text-blue-900' : 'bg-gray-200 text-gray-700'
                          }`}>
                            User #{row.buyer_user_id}
                          </span>
                        </td>
                        
                        <td className="px-4 py-3 text-sm">
                          <span className={`px-2 py-1 rounded-full text-xs font-semibold ${
                            isSeller ? 'bg-purple-200 text-purple-900' : 'bg-gray-200 text-gray-700'
                          }`}>
                            User #{row.seller_user_id}
                          </span>
                        </td>
                        
                        <td className="px-4 py-3 text-sm font-mono text-gray-800">{row.buyer_transaction_id}</td>
                        <td className="px-4 py-3 text-sm font-mono text-gray-800">{row.seller_transaction_id}</td>
                        <td className="px-4 py-3 text-sm font-bold text-green-600">${row.seller_price.toFixed(2)}</td>
                        <td className="px-4 py-3 text-sm font-bold text-blue-600">${row.buyer_price.toFixed(2)}</td>
                        <td className="px-4 py-3 text-sm text-gray-800">{row.seller_qty}</td>
                        <td className="px-4 py-3 text-sm text-gray-800">{row.buyer_qty}</td>
                        <td className="px-4 py-3 text-sm text-gray-800 font-semibold">{row.time_taken}</td>
                        <td className="px-4 py-3 text-sm">
                          <span className="px-2 py-1 bg-green-500 text-white text-xs font-bold rounded-full">
                            {row.status}
                          </span>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          )}

          {!loading && !error && data.length > 0 && (
            <div className="mt-6 text-center text-gray-600 text-sm">
              Showing {data.length} matched {data.length === 1 ? 'order' : 'orders'}
              {userIdFilter && currentUser && (
                <span className="ml-2 text-blue-600 font-semibold">
                  for {currentUser.username}
                </span>
              )}
              <span className={`ml-2 ${userIdFilter ? 'text-blue-600' : 'text-green-600'} font-semibold`}>
                (Auto-refreshes every 3 seconds)
              </span>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}